<template>
  <v-expansion-panels>
    <v-expansion-panel>
      <v-expansion-panel-header
        accordion
        class=""
        style="padding: 2px 16px; font-size: 16px"
        :disable-icon-rotate="critical"
      >
        Voltage
        <template v-slot:actions v-if="critical">
          <v-icon color="error"> mdi-alert-circle </v-icon>
        </template>
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-list dense style="column-count: 2">
          <v-list-item v-for="(key, index) in outlets.voltages" :key="index">
            <v-list-item-icon
              ><v-icon :color="get_color(index)"
                >mdi-power-plug-outline</v-icon
              ></v-list-item-icon
            >
            <v-list-item-content>{{ index }}</v-list-item-content>
            <v-chip :color="get_color(index)" dark>
              {{ `${outlets.voltages[index]}` }}
            </v-chip>
            <v-chip :color="get_color(index)" dark>
              {{ `${outlets.voltages[index]}` }}
            </v-chip>
          </v-list-item>
        </v-list>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
export default {
  props: ["outlets"],
  data: function () {
    return {
      critical: false,
    };
  },
  methods: {
    get_color(index) {
      this.critical = this.outlets.voltages.some((volt) => {
        return volt !== "?" && (volt > 240 || volt < 200);
      });

      if (
        this.outlets.voltages[index] > 240 ||
        this.outlets.voltages[index] < 200
      )
        return "red";
      if (this.outlets.voltages[index] === "?") return "grey";
      return "green";
    },
  },
};
</script>